<template>
    <div :class="loading ? 'loader-main' : ''">
        <div v-if="!loading">
            <!-- Nav Bar -->
            <nav class="navbar navbar-expand-lg navbar-light bg-light">
            <a class="navbar-brand" href="#">AntGate</a>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNav">
                <ul class="navbar-nav">
                <li class="nav-item active">
                    <a class="nav-link" href="#/">Home <span class="sr-only">(current)</span></a>
                </li>
                <li class="nav-item active">
                    <a class="nav-link" href="#/addCategory">Add Category <span class="sr-only">(current)</span></a>
                </li>
                <li class="nav-item active">
                    <a class="nav-link" href="#/category">Category <span class="sr-only">(current)</span></a>
                </li>
                <li class="nav-item active">
                    <a class="nav-link" href="#/books">Books <span class="sr-only">(current)</span></a>
                </li>
                </ul>
            </div>
            </nav>
            

            <!-- Body -->
            <div v-for="(book,index) in booksList" :key="index" class="card mt-5 mr-3 ml-3">
                <BookItem :book="book" @refresh="refreshBooks"></BookItem>
            </div>

            <!-- Pagination -->
            <nav aria-label="Page navigation example" class="d-flex justify-content-center mt-2 mb-2">
                <ul class="pagination">
                    <li @click="refreshBooks(paginator['currentPage']-1)" :class="paginator['hasPrevPage'] ? 'page-item visible' : 'page-item invisible'"><a class="page-link">{{paginator["currentPage"]-1}}</a></li>
                    <li class="page-item active">
                    <a class="page-link">{{paginator["currentPage"]}} <span class="sr-only">(current)</span></a>
                    </li>
                    <li @click="refreshBooks(paginator['currentPage']+1)" :class="paginator['hasNextPage'] ? 'page-item visible' : 'page-item invisible'"><a class="page-link">{{paginator["currentPage"]+1}}</a></li>
                </ul>
            </nav>
        </div>

        <!-- Spinner -->
        <SpinnerLoading :loading="loading"></SpinnerLoading>
    </div>
</template>

<script>
import axios from 'axios'
import BookItem from './BookItem.vue'
import SpinnerLoading from './SpinnerLoading.vue'

export default {
    components : {
        BookItem,
        SpinnerLoading,
    },
    data(){
        return {
            booksList : [],
            paginator : null,
            loading : false,
        }
    },
    created(){
        //Fetch Data From Server
        this.refreshBooks(1);
    },
    methods : {
        async refreshBooks(page){
            console.log(page)
            this.loading = true;
            const response = await axios.get(`${this.$hostname}/books?page=${page}`);
            this.booksList = response['data']['data']['bookList'];
            this.paginator = response['data']['data']['paginator'];
            this.loading = false;
        },
    }
}
</script>

<style scoped>
.btn-custom {
    margin-top: 2rem;
    width: 50%;
}
</style>