<template>
    <div>
        <img class="card-img-top custom-img" src="../assets/free_pen.jpg" alt="Card image cap">
        <div class="card-body">
            <h5 class="card-title">{{book['title']}}</h5>
            <p class="card-text custom-text">{{book['body']}}</p>
            <a @click="deleteBook" class="btn btn-danger btn-custom">Delete</a>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    props : ["book"],
    methods : {
        async deleteBook() {
            const payload = {
                "id" : this.book['_id']
            }
            //Delete Book From Server
            await axios.post(`${this.$hostname}/deleteBook`,payload);

            this.$emit('refresh');
        }
    }
}
</script>

<style scoped>
.custom-img {
    height: 120px;
}

.custom-text {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    line-clamp: 2; 
    -webkit-box-orient: vertical;
}
</style>